@import "../vars";
.mbtn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    &:focus {
        box-shadow: none;
    }
}

.mbtn-accent{
    padding: 0 67px;
    height: 59px;
    background-color: $primary;
    color: #fff;
    font-weight: 700;
    font-size: 20px;
    text-transform: uppercase;
    border-radius: 5px;
    &:hover{
        background-color: darken($primary,10%);
        color: #fff;
    }
}
