.main{
  background-image: url(../img/main-bg.webp);
  background-size: cover;
  background-position: center;
  padding: 40px 0 45px;
  color: #fff;
  &__inner{
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-items: center;
    gap: 10px;
  }
  &__left{
    display: flex;
    flex-direction: column;
    max-width: 583px;
    align-items: center;
  }
  &__img{
    width: 361.28px;
    display: block;
    margin: 0 auto 5px;
    height: auto;
  }
  &__title2{
    text-align: center;
  }
  &__title{
    width: 100%;
  }
  &__desc{
    margin-top: 20px;
    width: 100%;
  }
  &__btn{
    margin: 25px auto 0 0;
  }
}

.what{
  padding: 50px 0 55px;
  &__inner{
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__title{
    width: 100%;
  }
  &__desc{
    margin: 20px auto 23px 0;
    max-width: 862px;
  }
}

.how{
  background-color: $secondary;
  .what__desc{
    max-width: 972px;
  }
}

.honest{
  .what__desc{
    max-width: 964px;
  }
}

.where{
  .what__desc{
    max-width: 972px;
  }
}


@media screen and (max-width: 992px){
  .main{
    background-color: $secondary;
    padding: 0;
    background-image: none;
    &__inner{
      grid-template-columns: 100%;
      gap: 0;
    }
    &__left{
      padding: 25px 0 32px;
      color: $black;
      max-width: 100%;
    }
    &__right{
      order: -1;
      background-image: url(../img/main-bg.webp);
      background-size: cover;
      background-position: center;
      width: calc(100% + 20px);
      margin-left: -10px;
      padding: 20px 10px 30px;
    }
    &__img{
      max-width: calc(100% - 80px);
      margin-bottom: 0;
      transform: translateX(-14%);
    }
    &__btn{
      margin-top: 30px;
      width: 100%;
      padding: 0;
    }
    &__title2{
      margin-top: -15px;
      position: relative;
      z-index: 2;
    }
  }

  .what{
    padding: 30px 0 38px;
    &__btn{
      width: 100%;
      padding: 0;
    }
  }
}
